import * as React from 'react';

import { ResultsResponse, useDocumentTitle, useRequest } from '@miq/hookjs';
import Staff from '@miq/staffjs';
import { uuid } from '@miq/utiljs';
import { Loading, ViewSection } from '@miq/componentjs';
import { Customer, TCustomer, TCustomerInstance } from './utils';
import { CustomerCard } from './components';

export default function CustomerListView() {
  useDocumentTitle('Customers - Sales');
  const { res, loading } = useRequest(() => Customer().list());

  if (loading) return <Loading />;

  const r = new ResultsResponse<TCustomer>(res);
  if (!r.isSuccess) return null;

  return (
    <Staff.View
      title="Clients"
      back="/staff/sales/"
      className="sales-index-view miq-container center"
      headerCN="miq-container center"
    >
      <div className="d-grid">
        {r.items.map((data) => (
          <CustomerListItem data={data} key={uuid()} />
        ))}
      </div>
    </Staff.View>
  );
}

const CustomerListItem = ({ data }: { data: TCustomer | TCustomerInstance }) => {
  // const cust = Customer(data);
  return (
    <ViewSection
      border
      title={<CustomerCard data={data} />}
      // actions={
      //   <>
      //     <CustomerUpdateLink data={data} className="btn btn-primary-3 btn-square">
      //       <Icons.Edit />
      //     </CustomerUpdateLink>
      //   </>
      // }
      className="cust-list-item"
    />
  );
};
